.sub_visual {
    background-repeat: no-repeat;
    background-position: 0 0;
    background-size: cover;
    overflow-y: hidden;
}

.sub_visual .inner {
    position: relative;
    height: 300px;
    position: relative;
    top: 0;
    text-decoration: none;
}

.sub_visual .con_Box h2 {
    line-height: 240px;
    font-size: 2.5rem;
    font-weight: 600;
    color: #fff;
    text-align: center;
}

.sub_visual .inner h2 {
    line-height: 240px;
    font-size: 2.5rem;
    font-weight: 600;
    color: #fff;
    text-align: center;
}

.sub_visual .inner .sub_tab {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    margin: 0 auto;
}

.sub_visual .inner .sub_tab:after {
    display: block;
    clear: both;
    *zoom: 1;
    content: '';
}

.sub_visual .inner .sub_tab li {
    float: left;
    box-sizing: border-box;
    border: 1px solid #ddd;
    border-left: 0;
    transition: all 0.4s;
}

.sub_visual .inner .sub_tab li a {
    display: block;
    width: 100%;
    height: 60px;
    line-height: 60px;
    font-size: 1.125rem;
    color: #666;
    text-align: center;
    background-color: #fff;
    transition: all 0.4s;
    text-decoration: none;
}

.sub_visual .inner .sub_tab li.on {
    border: 1px solid #2789F6;
}

.sub_visual .inner .sub_tab li.on a {
    background-color: #2789F6;
    color: #fff;
}

.sub_visual .inner .sub_tab li:hover {
    border: 1px solid #2789F6;
}

.sub_visual .inner .sub_tab li:hover a {
    background-color: #2789F6;
    color: #fff;
}

