#toggle_menu_tabs {
    display: table;
    width: 100%;
    height: 50px;
    border: 3px;
    border-spacing: 1px;
    text-align: center;
}

.toggle-menu-tab {
    display: table-cell;
    height: 30px;
    vertical-align: middle;
    cursor: pointer;
    padding: 5px 10px;
    box-shadow: 1px -4px 1px 0 darkgray inset;
}

.toggle-menu-tab:hover {
    background-color: lightcoral;
}

.toggle-menu-tab:hover::before {
    content: "*";
    display: inline-block;
}

.toggle-menu-tab:hover::after {
    content: "*";
    display: inline-block;
}

.toggle-menu-tab:first-child {
    border-top-left-radius: 20px;
    border-bottom-left-radius: 20px;
}

.toggle-menu-tab:last-child {
    border-top-right-radius: 20px;
    border-bottom-right-radius: 20px;
}

#toggle_menu_views {
    box-sizing: border-box;
    height: 300px;
    padding: 10px;
    border: 1px dotted gray;
}

.toggle-menu-view {
    display: none;
}

input[name=toggle_menu_sel]:checked+div.toggle-menu-view {
    display: block;
}


